img {
  max-width: 540px;
  max-height: 90vh;
  -o-object-fit: contain;
     object-fit: contain;
  border: 4px solid #424242;
  overflow: hidden;
}

@media only screen and (min-width: 320px) and (max-width: 479px){
  img {
    max-width: 320px;
    max-height: 90vh;
    -o-object-fit: contain;
       object-fit: contain;
    border: 4px solid #424242;
    overflow: hidden;
    z-index: 0;
    position: relative;
  }
 }



